nav.nav>ul{
    
    width:150%;
    display: flex;
    flex-direction: column;
    line-height: 50px;
    list-style-type: none;
}
nav.nav{
    width:50%
}
.nav-link{
    display:block;
    height: 100%;
    letter-spacing: 2px;
    font-size: 75%;
    font-weight: 100;
    color: white;
    text-decoration: none;
    transition-duration: 0.2s;
}
.nav-link.active{
    font-size: 100%;
    font-weight: bold;
}
.nav-link:hover{
    font-size: 90%;
}
.nav-link.active:hover{
    font-size: 100%;
}
.sec1{
    display: flex;
    justify-content: center;
}