.content_wrapper{
    justify-self: center;
    text-align: center;
    color: #ffffff;
    letter-spacing: 5px;
}
h3,h1{
    display: inline;
}
.content{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.word{
    margin: 0 10px;
}
.a4{
    animation: blink linear 0.7s infinite;
}

.a0{
    animation: blink linear 0.7s infinite .4s;
}
.a4_{
    animation: blink linear 0.7s infinite .2s;
}
.P{
    animation: blink linear 0.7s infinite .1s;
}
.A{
    animation: blink linear 0.7s infinite .3s;
}
.G{
    animation: blink linear 0.7s infinite .6s;
}
.E{
    animation: blink linear 0.7s infinite .2s;
}
.N{
    animation: blink linear 0.7s infinite .5s;
}
.O{
    animation: blink linear 0.7s infinite .2s;
}
.T{
    animation: blink linear 0.7s infinite .1s;
}
.F{
    animation: blink linear 0.7s infinite .3s;
}
.U{
    animation: blink linear 0.7s infinite .48s;
}
.D{
    animation: blink linear 0.7s infinite .4s;
}
@keyframes blink {
    0.0%{
    }
    39.9%{
        opacity: 1;
    }
    49.8%{
        opacity: 0;
    }
    60.3%{
        opacity: 1;
    }
}