.social_back {
  width: 12%;
  height: 5%;
  min-width: 150px;
  position: fixed;
  top: 0;
  right: 0;
    padding: 0.3%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-items: center;
    background: linear-gradient(
      258.18deg,
      rgba(42, 42, 114, 0.6) -3.29%,
      rgba(0, 41, 255, 0.6) 98.64%
    );
    box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.25);
  }
  .social_back>a{
    text-align: center;
  }
  .soc-icon {
    width: 50%;
  }
  