.progress {
  width: 70%;
  margin: 1% 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
}
.ico {
  /* background-image: url(./icons/html.png);
    background-size: cover; */
  height: 40px;
  width: 40px;
}
.progressBar {
  align-self: center;
  align-items: center;
  height: 20px;
  display: flex;
  width: 75%;
  background-color: #2a2a72;
  border-radius: 25px;
}
.progressAchieved {
  display: flex;
  flex-direction: column;
  margin-left: 3px;
  height: 15px;
  border-radius: 20px;
  justify-content: center;
  -webkit-box-shadow: inset 0px 0px 3px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: inset 0px 0px 3px 0px rgba(0, 0, 0, 0.75);
  box-shadow: inset 0px 0px 3px 0px rgba(0, 0, 0, 0.75);
  background: linear-gradient(270deg, #0029ff 0%, #009ffd 100%);
}
