.contact{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.contact>.alert>div{
    display: none;
    padding: 5%;
    width: 100%;
}
.alert>.success{
    display: block !important;
    background-color: #00ff2a33;
    border:1px solid #00ff2a;
    color:#00ff2a;
}
.alert>.danger{
    display: block !important;
    background-color: #ff080033;
    border:1px solid #ff0800;
    color:#ff0800;
}
.input-wrapper {
    display: flex;
    flex-direction: column;
    margin-bottom: 2%;
    width: 80%;
  color: white;
}
input,textarea,button {
    color: white;
    padding: 15px;
    border: 3px solid white;
  background: rgba(217, 217, 217, 0.15);
  transition-duration: 250ms ;
}
input::placeholder,textarea::placeholder{
    color: whitesmoke;
}
input:focus,textarea:focus{
    border-color: #0029FF;
    outline: none;
}
textarea{
    min-height: 100px;
    resize:vertical;
}
button[type="submit"]{
    background-color: white;
    color: black;
}
button:hover{
    border-color: #0029ff;
    background-color: #0029FF;
    color: white;
}