*{
  font-family:Arial, Helvetica, sans-serif;
  margin:0;
  padding: 0;
}
*::selection{
  background-color: #fff;
  background-image: linear-gradient(270deg, #0029ff 0%, #009ffd 100%);
  color: #0029ff;
}
.back_container {
  height: 100vh;
  width: 100vw;
  background-image: url("./../public/background.webp");
  background-size:cover;
  background-position-y: 80%;
  background-repeat: no-repeat;
  background-attachment:scroll;
}
.back_layer {
  background: rgba(42, 42, 114, 0.63);
  height: 100vh;
  width: 100vw;
}
.root-div{
  height: 100vh;
  display: grid;
  grid-template-columns: 15% auto 15%;
  grid-template-areas:"sec1 sec2 sec3" ;
}
section.sec1{
  align-self: center;
  grid-area: sec1;
}
section.sec2{
  align-self: center;
  align-content: center;
  grid-area: sec2;
}
section.sec3{
  grid-area: sec3;
}