.loading_wrapper{
    grid-area: sec2;
    justify-self: center;
    /* display: flex;
    flex-direction: row;
    justify-content: center; */
    align-self: center;
    text-align: center;
    position: relative;
    left: 0;
    top: 45%;
}
.loading_parent{
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0,0.8);
    height: 100vh;
    width: 100vw;
    animation-name: fade-out;
}
.loading_bar>span{
    margin: 0 3%;
    font-size: 30px;
    color: white;
}

.l{
    animation: l 3s infinite;
}
.o{
    animation: o 3s infinite;
}
.a{
    animation: a 3s infinite;
}
.d{
    animation: d 3s infinite;
}
.i{
    animation: i 3s infinite;
}
.n{
    animation: n 3s infinite;
}
.g{
    animation: g 3s infinite;
}
@keyframes l {
    0%{
        filter: blur(10px);
    }    
    30%{
        filter: blur(0px);
    }
    100%{
        filter: blur(10px);
    }
}
@keyframes o {
    0%{
        filter: blur(10px);
    }    
    40%{
        filter: blur(0px);
    }
    100%{
        filter: blur(10px);
    }
}
@keyframes a {
    0%{
        filter: blur(10px);
    }    
    50%{
        filter: blur(0px);
    }
    100%{
        filter: blur(10px);
    }
}
@keyframes d {
    0%{
        filter: blur(10px);
    }    
    60%{
        filter: blur(0px);
    }
    100%{
        filter: blur(10px);
    }
}
@keyframes i {
    0%{
        filter: blur(10px);
    }    
    70%{
        filter: blur(0px);
    }
    100%{
        filter: blur(10px);
    }
}
@keyframes n {
    0%{
        filter: blur(10px);
    }    
    80%{
        filter: blur(0px);
    }
    100%{
        filter: blur(10px);
    }
}
@keyframes g {
    0%{
        filter: blur(10px);
    }    
    90%{
        filter: blur(0px);
    }
    100%{
        filter: blur(10px);
    }
}